// This is the main with imports from base folder.
@import 'reset',
'typography',
'forms';

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  @include widescreen {
    width: 1150px;
  }
}

.btn {
  font: 16px/18px #{map-get($base, 'base-font-serif')};
  background-color: #{map-get($base, 'primary-color')};
  color: #dadada;
  border-radius: 10px;
  min-width: 130px;
  height: 49px;
  border: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  padding: 15px;
  @include tablet {
    font-size: 18px;
    min-width: 160px;
  }
  @include desktop {
    &:hover {
      transition: background-color .5s, color .5s;
      color: #{map-get($base, 'primary-color')};
      background-color: #{map-get($base, 'second-primary-color')};
    }
  }
}
