#footer {
  background-color: #{map-get($base, 'second-primary-color')};
  font-family: #{map-get($base, 'additional-font-family')};
}

.footer-wrap {
  padding: 20px;
  @include phone {
    font-size: 0;
  }
  @include widescreen {
    padding: 98px 0 78px;
    max-width: 1120px;
    margin: 0 auto;
  }

  h4 {
    @include widescreen {
      margin-bottom: 46px;
    }
  }
  p {
    line-height: 20px;
  }
}

.footer-item {
  margin-bottom: 15px;
  @include phone {
    width: 50%;
    display: inline-block;
    vertical-align: top;
    font-size: 15px;
    padding-left: 20px;
    padding-right: 10px;
    &:first-child {
      padding-left: 10px;
      h4 {
        position: relative;
        &:after {
          content: '';
          width: 15px;
          height: 2px;
          background-color: #{map-get($base, 'primary-color')};
          position: absolute;
          left: 0;
          bottom: 0;
        }
      }
    }
  }
  @include desktop {
    width: 25%;
  }
}

.footer-nav {
  margin: 0;
  li {
    margin-bottom: 10px;
  }
  a {
    @include desktop {
      position: relative;
      padding-bottom: 1px;
      &:hover {
        &:after {
          width: 100%;
        }
      }
    }
    &:after {
      content: '';
      width: 0;
      height: 1px;
      background-color: #{map-get($base, 'primary-color')};
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      transition: width .3s;
    }
  }
}

.footer-addition {
  background-color: #{map-get($base, 'primary-color')};
  text-align: center;
  padding: 16px 20px;
  p {
    margin: 0;
    color: #{map-get($base, 'second-primary-color')};
    font-family: #{map-get($base, 'base-font-serif')};
    font-weight: 700;
  }
}