.menu-active {
  .menu-btn {
    .plus {
      &:before {
        transform: rotate(-90deg);
      }
    }
  }
  .main-navigation {
    transform: translateX(0);
    @include tablet {
      //width: calc(100% - 180px);
    }
    li {
      @include tablet {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
}

.sticky {
  transition: background-color .3s;
  background-color: rgba(211, 211, 211, .7);
  z-index: 100;
}

#header {
  position: fixed;
  left: 0;
  right: 0;
}

.header-holder {
  padding: 10px 60px 10px 14px;
  position: relative;
  @include tablet {
    overflow: hidden;
    padding-top: 27px;
  }
  @include widescreen {
    max-width: 1138px;
    margin: 0 auto;
  }
}

.main-logo {
  display: block;
  font: 40px/31px #{map-get($base, 'base-font-serif')};
  position: relative;
  @include desktop {
    max-width: 97px;
    overflow: hidden;
    padding-bottom: 5px;
    float: left;
    &:hover {
      &:after {
        width: 100%;
      }
    }
  }

  &:after {
    content: '';
    width: 15px;
    height: 4px;
    background-color: #{map-get($base, 'primary-color')};
    display: block;
    position: absolute;
    left: 7px;
    @include desktop {
      transition: width .3s;
    }
  }
}

.menu-btn {
  display: block;
  position: absolute;
  padding: 12px 0 18px;
  width: 36px;
  right: 15px;
  top: 10px;
  z-index: 100;
  @include tablet {
    top: 20px;
  }
  .line {
    display: block;
    @include menu-btn;
    &:before,
    &:after {
      content: '';
      position: absolute;
      @include menu-btn;
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 6px;
    }
  }

  .plus {
    display: block;
    width: 19px;
    height: 19px;
    background-color: #{map-get($base, 'primary-color')};
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    &:before,
    &:after {
      content: '';
      position: absolute;
      background-color: #{map-get($base, 'base-background-color')};
    }
    &:before {
      width: 3.5px;
      height: 10px;
      left: 7.75px;
      top: 4.5px;
      transition: transform .3s;
    }
    &:after {
      width: 10px;
      height: 3.5px;
      top: 7.75px;
      left: 4.5px;
    }
  }
}

.main-navigation {
  position: absolute;
  height: calc(100vh - 51px);
  top: 51px;
  background-color: #{map-get($base, 'base-background-color')};
  left: 0;
  right: 0;
  padding: 20px 14px;
  overflow: auto; 
  transform: translateX(-100%);
  transition: transform .3s, width .5s;
  @include tablet {
    background-color: transparent;
    height: auto;
    padding: 0;
    top: 18px;
    right: 92px;
    left: 120px;
    text-align: right;
    transform: translateX(0%);
    overflow: hidden;
  }
  ul {
    margin: 0;
  }
  li {
    font: 700 17px/17px #{map-get($base, 'additional-font-family')};
    margin-bottom: 20px;
    @include tablet {
      display: inline-block;
      transform: translateX(1000px);
      transition: transform .6s, opacity .5s;
      opacity:0;
      margin: 0 1px;
    }
  }

  .active {
    @include tablet {
      background-color: #{map-get($base, 'primary-color')};
      color: #{map-get($base, 'second-primary-color')};
      border-radius: 10px;
    }
  }

  a {
    @include tablet {
      display: block;
      padding: 8px 22px;
      &:hover {
        background-color: #{map-get($base, 'primary-color')};
        color: #{map-get($base, 'second-primary-color')};
        border-radius: 10px;
        transition: background-color .5s, color .5s;
      }
    }
  }
}