#portfolio-section {
  text-align: center;
  padding: 30px 0;
  @include desktop {
    padding-top: 105px;
    padding-bottom: 100px;
  }
}

.projects-wrap {
  padding: 5px 0;
  @include phone {
    overflow: hidden;
    margin: 0 -15px;
  }
  @include desktop {
    padding-top: 37px;
  }
}

.project-item {
  position: relative;
  height: 221px;
  margin-bottom: 18px;
  @include phone {
    width: 50%;
    padding: 5px 15px;
    float: left;
  }
  @include tablet {
    width: 33.3333%;
  }
  @include desktop {
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: 5px;
      right: 15px;
      left: 15px;
      bottom: 5px;
      opacity: 0;
      transition: opacity .5s;
      background-color: rgba(62, 62, 62, .38);
      z-index: 1;
    }
    &:hover {
      &:after {
        opacity: 1;
      }
      .project-info {
        transform: translateY(0);
      }
      .project-actions {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
  &.animated {
    animation: zoomIn 1s both;
  }
  &.big {
    height: 460px;
    @include desktop {
      .project-info {
        height: 100px;
        padding-top: 29px;
      }
    }
    .project-actions {
      top: 39%;
    }
  }
  + .big {
    @include phone {
      float: right;
    }
  }
}

.project-preview {
  height: 100%;
  background: 50% 50% / cover no-repeat;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, .28);
}

.project-actions {
  position: absolute;
  left: 0;
  right: 0;
  top: 30%;
  background-color: rgba(0, 0, 0, .3);
  z-index: 2;
  @include phone {
    right: 15px;
    left: 15px;
  }
  @include desktop {
    transform: translateX(-100%);
    opacity: 0;
    transition: transform .6s, opacity .3s;
    background-color: transparent;
  }
}

.view,
.like {
  width: 40px;
  height: 40px;
  background: 50% 50% / cover no-repeat;
  display: inline-block;
  margin: 0 15px;
}

.view {
  background-image: url("../images/eye-icon.png");
}

.like {
  background-image: url("../images/heart-icon.png");
}

.project-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 15px;
  background-color: #d3d3d3;
  max-height: 100px;
  overflow: hidden;
  text-align: left;
  @include phone {
    left: 15px;
    right: 15px;
  }
  @include desktop {
    transform: translateY(200%);
    transition: transform .3s;
    height: 72px;
    z-index: 20;
    padding-left: 29px;
    padding-right: 29px;
  }

  h5 {
    margin-bottom: 0;
  }

  p {
    font-style: italic;
    font-family: #{map-get($base, 'additional-font-family')};
    margin-bottom: 0;
  }
}

.see-all {
  margin-top: 30px;
  @include desktop {
    margin-top: 69px;
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }

  50% {
    opacity: 1;
  }
}

