// This is base reset for form elements.
//
// Note: If you use a framework that includes form styling then you need to remove this file.

form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
textarea {
  -webkit-appearance: none;
  //-webkit-border-radius: 0;
  box-sizing: border-box;
  border: 1px solid map-get($forms, 'form-element-border-color');
  padding: map-get($forms, 'form-element-padding');

  &:focus {
    border-color: map-get($forms, 'form-element-focus-border-color');
  }

  @include placeholder {
    color: map-get($forms, 'placeholder-color');
  }
}

select {
  -webkit-border-radius: 0;
}

textarea {
  resize: vertical;
  vertical-align: top;
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
  -webkit-appearance: none;
  //-webkit-border-radius: 0;
  cursor: pointer;
}

.form-control {
  height: 41px;
  outline: none;
  transition: border-color .3s
}

#subscribe {
  padding-top: 14px;
}

.subscribe-group {
  position: relative;
  .form-control {
    width: calc(100% - 95px);
    display: block;
    border-radius: 10px 0 0 10px;
    &.error {
      border-color: #c00000;
    }
  }
  .btn {
    position: absolute;
    top: 0;
    right: 0;
    width: 95px;
    min-width: 95px;
    border-radius: 0 10px 10px 0;
    height: 41px;
    font-size: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  label.error {
    font-size: 14px;
    display: block;
    padding: 0.4em 0.7em;
  }
}