#about {
  background-color: #e9e9e9;
  padding: 20px 0;
  @include desktop {
    padding-top: 108px;
    padding-bottom: 60px;
  }
  .container {
    @include widescreen {
      width: 1170px;
    }
  }
}

.carousel-wrap {
  @include desktop {
    overflow: hidden;
    position: relative;
  }
}

.about-carousel {
  padding: 0 15px;
  @include desktop {
    overflow: hidden;
  }
}

.description {
  box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.14);
  padding: 26px 20px;
  border-radius: 10px;
  margin: 10px 10px 30px;
  @include desktop {
    width: 50%;
    float: left;
    overflow: hidden;
    margin-right: 54px;
    padding: 56px 52px 56px 66px;
  }
  p {
    font-family: #{map-get($base, 'additional-font-family')};
    margin: 0;
  }
  .accent-part {
    font: 700 30px/20px #{ map-get($base, 'base-font-serif')};
  }
}

.slide-item {
  @include desktop {
    padding-top: 12px;
    outline: none;
  }
  h4 {
    @include desktop {
      padding-right: 40px;
      margin-bottom: 28px;
    }
  }
  p {
    line-height: 24px;
  }
}

.slider-nav {
  width: 60px;
  padding-left: 15px;
  @include desktop {
    width: 36px;
    padding: 0;
    position: absolute;
    right: 0;
    top: 3px;
  }
}

.slick-arrow {
  width: 30px;
  height: 30px;
  display: inline-block;
  vertical-align: top;
  background: url("../images/arrow.svg") 50% 50% / cover no-repeat;
  @include desktop {
    width: 26px;
    height: 24px;
  }

  &.prev {
    transform: rotate(180deg);
  }

  &.slick-disabled {
    opacity: .2;
  }
}