// Global variables
//
// Base font styles for headings and links.

// For headers
$headings: (
        'sizes': (
                'h1-font-size': 40px,
                'h2-font-size': 35px,
                'h3-font-size': 25px,
                'h4-font-size': 20px,
                'h5-font-size': 18px,
                'h6-font-size': 15px
        ),
        'fonts': (
                'headings-font-family': unquote("'Arvo','Times New Roman', Times, Baskerville, Georgia, serif"),
                'headings-color': inherit
        )
);

// Global vars for body typography
$base: (
        'base-font-sans-serif': unquote("'Poppins', 'Roboto', Helvetica, sans-serif"),
        'base-font-serif': unquote("'Arvo','Times New Roman', Times, Baskerville, Georgia, serif"),
        'primary-color': #3e3e3e,
        'second-primary-color': #d3d3d3,
        'base-text-color': #3e3e3e,
        'base-background-color': #fff,
        'base-link-color':#3e3e3e,
        'base-font-size': 15px,
        'base-line-height': 1.2,
        'base-font-family': unquote("'Poppins', 'Roboto',Arial, 'Helvetica Neue', Helvetica, sans-serif"),
        'additional-font-family': unquote("'Roboto',Arial, 'Helvetica Neue', Helvetica, sans-serif"),
        'vertical-rhythm': '(map-get($base, base-line-height) * 1em)',
        'base-min-width': 320px
);

// Vars for forms
$forms: (
        'form-element-padding': unquote("0.4em 0.7em"),
        'form-element-border-color': transparent,
        'form-element-focus-border-color': #3e3e3e,
        'placeholder-color': #d3d3d3
);

// Breakpoints for include media mixin
$breakpoints: (
        'phone': 480px,
        'tablet': 768px,
        'desktop': 980px,
        'widescreen': 1200px
);

// Keywords for include media mixin
$media-expressions: (
        'screen': 'screen',
        'print': 'print',
        'handheld': 'handheld',
        'landscape': '(orientation: landscape)',
        'portrait': '(orientation: portrait)',
        'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
        'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)'
);