#top-section {
  background: 50% 100% / cover no-repeat;
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  @include desktop {
    height: 652px;
  }

  .container {
    max-height: 400px;
    overflow: auto;
    @include tablet {
      max-width: 550px;
    }
    @include desktop {
      padding-top: 84px;
    }
  }
  .buttons-block {
    max-width: 350px;
    margin: 0 auto;
  }
  h1 {
    margin-bottom: 3px;
  }
  h2 {
    font-weight: 400;
    margin-bottom: 28px;
  }
  p {

  }
}

.buttons-block {
  display: flex;
  justify-content: space-between;
  @include desktop {
    padding-top: 36px;
  }
  .btn {
    width: 130px;
  }
}